import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../components/panel/panel.jsx";
import HeadTerimaServiceLuar from "./HeadTerimaServiceLuar.jsx";
import ModalGlobal from "../../ModalGlobal.jsx";
import ModalTerimaServiceLuar from "./ModalTerimaServiceLuar.jsx";
import { showModal } from "../../../actions/datamaster_action.jsx";
import Tabel from "../../../components/Tabel/tabel.jsx";

const maptostate = (state) => {
  return {
    kunci_temp: state.stocking.kunci_temp,
  };
};

class ServiceLuar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      bayar: false,
      columns: [
        {
          dataField: "nama_supplier",
          text: "Nama Partner",
        },
        {
          dataField: "alamat",
          text: "Alamat",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "qty",
          text: "Qty",
        },
        {
          dataField: "keterangan_service",
          text: "Keterangan Service",
        },
        {
          dataField: "harga_satuan",
          text: "Harga Satuan",
        },
        {
          dataField: "total",
          text: "Total",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            this.setState({});
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.props.dispatch(showModal())}
                    className="btn btn-primary mr-3"
                  >
                    Simpan
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
      data: [
        {
          nama_supplier: "Nagatech Motor",
          alamat: "ARIA GRAHA",
          nama_barang: "BAN FIRELLI",
          qty: 4,
          keterangan_service: "GANTI BAN",
          harga_satuan: 500000,
          total: 2000000,
        },
      ],
    };
  }

  componentDidMount() {}
  handleSubmit(data) {
    console.log(data);
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Transaksi</Link>
          </li>
          <li className="breadcrumb-item active">Terima Service Luar</li>
        </ol>
        <h1 className="page-header">Terima Service Luar </h1>
        <div className="row">
          <div className="col-lg-12">
            <Panel>
              <PanelHeader>Terima Service Luar</PanelHeader>
              <PanelBody>
                <br />
                <div className="col-lg-12">
                  <HeadTerimaServiceLuar />
                </div>
                <br />
                <div className="col-lg-12">
                  <Tabel
                    keyField="nama_supplier"
                    data={this.state.data || []}
                    columns={this.state.columns}
                    empty={true}
                    emptyText="Silahkan Masukan Nomor Service dan klik cari"
                  />
                </div>
                {/* End Tambah Master Kategori  */}
              </PanelBody>
            </Panel>
            <ModalGlobal
              title="Simpan Data Harga"
              content={<ModalTerimaServiceLuar />}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(ServiceLuar);
